import React, { Component } from "react";
import { Menu, Segment, Image, Button, Icon } from "semantic-ui-react";
import HomeScreen from "../images/pubgfreefire.jpg";
import "./Home.css";

export class Home extends Component {
  render() {
    return (
      <div>
        <Segment className="HomeScreen">
          <div>
            <Image src={HomeScreen} />
            <div className="overlay"></div>
            <span className="Home__toptext">Welcome to</span>
            <span className="Home__bottomtext">Host it</span>
          </div>
        </Segment>
      </div>
    );
  }
}

export default Home;
