import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Menu, Segment, Button, Icon } from "semantic-ui-react";
import "./App.css";
import Home from "./components/Home";
import Videos from "./components/Videos";
import Contact from "./components/Contact";

export class App extends Component {
  state = { activeItem: "home", loggedIn: true };

  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name });
  };

  render() {
    const { activeItem, loggedIn } = this.state;

    return (
      <div>
        <Menu pointing secondary className="app__nav">
          <Menu.Item
            icon="home"
            name="home"
            active={activeItem === "home"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="videos"
            icon="video play"
            active={activeItem === "videos"}
            onClick={this.handleItemClick}
          />
          <Menu.Item
            name="contact"
            icon="phone"
            active={activeItem === "contact"}
            onClick={this.handleItemClick}
          />
          <Menu.Menu position="right">
            {loggedIn ? (
              <Menu.Item>
                <Button Mini color="green" basic>
                  <Link style={{ color: "green" }} to="./components/login">
                    <Icon name="sign-in" />
                    Signin
                  </Link>
                </Button>
              </Menu.Item>
            ) : (
              <Menu.Item className="app__signout">
                <Button color="red" basic icon>
                  <Icon name="sign-out" />
                  Logout
                </Button>
              </Menu.Item>
            )}
          </Menu.Menu>
        </Menu>

        <Segment>
          {activeItem === "home" ? <Home /> : null}
          {activeItem === "videos" ? <Videos /> : null}
          {activeItem === "contact" ? <Contact /> : null}
        </Segment>
      </div>
    );
  }
}
export default App;
