import React, { Component } from "react";
import { Icon } from "semantic-ui-react";

import "./Contact.css";
export class Contact extends Component {
  render() {
    return (
      <section className="contact">
        <div className="content">
          <h2>Contact Us</h2>
          <p>
            Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ab
            repudiandae sed minus magnam voluptate, nemo repellendus fugiat
            excepturi eligendi. Doloribus?
          </p>
        </div>
        <div className="container">
          <div className="contactInfo">
            <div className="box">
              <div className="icon">
                <Icon name="map marker alternate" color="orange" />
              </div>
              <div className="text">
                <h3>Address</h3>
                <p>K.K.Nagar,Chennai-600078,TamilNadu.</p>
              </div>
            </div>
            <div className="box">
              <div className="icon">
                <Icon name="phone" color="orange" />
              </div>
              <div className="text">
                <h3>Phone</h3>
                <p>+91 98409 35039</p>
              </div>
            </div>
            <div className="box">
              <div className="icon">
                <Icon name="mail" color="orange" />
              </div>
              <div className="text">
                <h3>Email</h3>
                <p>hostitgamingcompany@gmail.com</p>
              </div>
            </div>
          </div>
          <div className="contactForm">
            <form action="includes/contactus.inc.php" method="post">
              <h2>Send Message</h2>

              <div className="inputBox">
                <input type="text" name="name" required="required" />
                <span>Full Name</span>
              </div>
              <div className="inputBox">
                <input type="text" name="email" required="required" />
                <span>Email</span>
              </div>
              <div className="inputBox">
                <textarea
                  name="comment"
                  id=""
                  maxlength="255"
                  cols="30"
                  rows="10"
                  required="required"
                ></textarea>
                <span>Type your message.(max 255 characters)</span>
              </div>
              <div className="inputBox">
                <input
                  type="submit"
                  name="contactus"
                  value="send"
                  required="required"
                />
              </div>
            </form>
          </div>
        </div>
      </section>
    );
  }
}

export default Contact;
